import React from 'react';
import { users } from 'data/users';
import { UsersListItem } from 'components/UsersListItem';
import styled from 'styled-components';

const Wrapper = styled.ul`
  list-style: none;
  padding: 0 24px 0 50px;
  max-width: 500px;
  margin: 0;
  width: 100%;
  min-height: 700px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* align-items: flex-start; */
  background-color: ${({ theme }) => theme.colors.white};
  color: ${({ theme }) => theme.colors.darkGrey};
  border-radius: 10px;
  box-shadow: -2px 4px 10px rgba(115, 124, 142, 0.09);
`;

const Button = styled.button`
  align-self: center;
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  cursor: pointer;
  box-shadow: -2px 4px 10px rgba(115, 124, 142, 0.09);
  outline: none;
`;

export class UserList extends React.Component {
  state = {
    islistTitle: true,
    users,
  };

  changeTitle = () => {
    this.setState((prevState) => ({
      islistTitle: !prevState.islistTitle,
    }));
  };

  deleteUser = (name) => {
    const filteredUsers = this.state.users.filter((user) => name !== user.name);
    this.setState({ users: filteredUsers });
  };

  render() {
    const { users } = this.state;
    return (
      <Wrapper>
        <h1>{this.state.islistTitle ? 'Users List' : 'Students List'}</h1>

        <Button onClick={this.changeTitle}>Change Title</Button>

        {users.map((user) => {
          return <UsersListItem deleteUser={this.deleteUser} userData={user} />;
        })}
      </Wrapper>
    );
  }
}
