import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { Button } from 'components/Button';

const StyledLi = styled.li`
  display: flex;
  padding: 30px 0;
  position: relative;
  &:not(:last-child)::after {
    display: flex;
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    bottom: 0;
    left: 0;
    background-color: #dfe2e8;
  }
`;

const StyledName = styled.p`
  font-weight: 700;
  color: ${({ theme }) => theme.colors.darkGrey};
  font-size: ${({ theme }) => theme.fontSize.l};
`;

const StyledAverage = styled.div`
  margin-right: 24px;
  width: 34px;
  height: 34px;
  border-radius: 50px;
  font-size: ${({ theme }) => theme.fontSize.m};
  font-weight: 700;
  background-color: ${(props) => (props.children <= 3 ? (props.children < 2 ? '#CB8581' : '#E1D888') : '#8FCB81')};
  display: flex;
  justify-content: center;
  align-items: center;
  color: ${({ theme }) => theme.colors.white};
`;

const StyledAttendance = styled.p`
  font-size: ${({ theme }) => theme.fontSize.m};
  font-weight: 400;
`;

export const UsersListItem = ({ deleteUser, userData: { name, attendance, average } }) => {
  return (
    <StyledLi>
      <StyledAverage>{average}</StyledAverage>
      <div>
        <StyledName>{name}</StyledName>
        <StyledAttendance>attendance: {attendance}</StyledAttendance>
      </div>
      <Button onClick={() => deleteUser(name)} />
    </StyledLi>
  );
};

UsersListItem.propTypes = {
  userData: PropTypes.shape({
    average: PropTypes.string,
    name: PropTypes.string,
    attendance: PropTypes.string,
  }),
};
